/* Styling for Body */

body {
  margin: 0 10 0 10;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Montserrat', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
   background-color:black;
}

/* Styling for Fonts on Responsive Web */ 

h1 {
   font-family: 'Overpass', sans-serif;
   -webkit-font-smoothing: antialiased;
   -moz-osx-font-smoothing: grayscale;
   color:white;
	    }

h2 {
   font-family: 'Overpass', sans-serif;
   -webkit-font-smoothing: antialiased;
   -moz-osx-font-smoothing: grayscale;
   color:white;
}

p {
   font-family: 'Chivo', sans-serif;
   -webkit-font-smoothing: antialiased;
   -moz-osx-font-smoothing: grayscale;
   color:white;
}

/* Styling for Fonts on Mobile */ 

@media only screen and (max-width: 480px) {h1 {font-size: 24px;}}
@media only screen and (max-width: 480px) {h2 {font-size: 18px;}}
@media only screen and (max-width: 480px) {p {font-size: 14px;}}

/* Styling for Fonts for Code */

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
